'use server';

import { cookies } from 'next/headers';
import { redirect } from 'next/navigation';
import { z } from 'zod';
import {
  mockOAuthLogin,
  createEvent as apiCreateEvent,
  joinEvent as apiJoinEvent,
  leaveEvent as apiLeaveEvent,
  checkInUser as apiCheckInUser,
  ADMIN_CREATE_EVENT_PASSWORD,
  getMockUserById,
} from './data';
import type { Event } from './types';

const MAX_AGE = 60 * 60 * 24 * 30; // 30 days

// --- Auth Actions ---
export async function login(prevState: any, formData: FormData) {
  const email = formData.get('email') as string; // Using email field as username for Blessing Skin Server
  const password = formData.get('password') as string;

  // Basic validation
  if (!email || !password) {
    return { message: 'Username and password are required.' };
  }

  const user = await mockOAuthLogin(email, password);

  if (user) {
    cookies().set('userId', user.id, {
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      maxAge: MAX_AGE,
      path: '/',
      sameSite: 'lax',
    });
    redirect('/profile');
  } else {
    return { message: 'Invalid username or password.' };
  }
}

export async function logout() {
  cookies().delete('userId');
  redirect('/login');
}

export async function getSessionUser() {
  const userId = cookies().get('userId')?.value;
  if (!userId) return null;
  try {
    return await getMockUserById(userId);
  } catch (error) {
    console.error("Failed to get session user:", error);
    return null;
  }
}

// --- Event Actions ---
const CreateEventSchema = z.object({
  name: z.string().min(3, "Event name must be at least 3 characters."),
  date: z.string().refine((date) => !isNaN(Date.parse(date)), "Invalid date format."),
  description: z.string().min(10, "Description must be at least 10 characters."),
  participantLimit: z.preprocess(
    (val) => (val === "" || val === null || val === undefined ? undefined : Number(val)),
    z.number().int().positive().optional()
  ),
  cdKey: z.string().min(4, "CD-Key must be at least 4 characters long.").max(20),
  imageUrl: z.string().url("Invalid image URL.").optional().or(z.literal('')),
  adminPassword: z.string().refine(pass => pass === ADMIN_CREATE_EVENT_PASSWORD, "Invalid admin password.")
});

export async function createEventAction(prevState: any, formData: FormData) {
  const rawFormData = Object.fromEntries(formData.entries());
  const validatedFields = CreateEventSchema.safeParse(rawFormData);

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: 'Failed to create event. Please check the fields.',
    };
  }
  
  const user = await getSessionUser();
  if (!user) {
    return { message: 'Authentication required.' };
  }

  try {
    const { adminPassword, ...eventData } = validatedFields.data;
    const newEvent = await apiCreateEvent(eventData as Omit<Event, 'id' | 'participants' | 'organizerId'>, user.id);
    // revalidatePath('/events'); // If using on-demand revalidation
    // revalidatePath('/');
    redirect(`/events/${newEvent.id}`);
  } catch (error) {
    console.error("Create event error:", error);
    return { message: 'Failed to create event on the server.' };
  }
}

export async function joinEventAction(eventId: string, cdKey: string) {
  const user = await getSessionUser();
  if (!user) {
    return { success: false, message: 'You must be logged in to join an event.' };
  }
  try {
    const result = await apiJoinEvent(user.id, eventId, cdKey);
    // revalidatePath(`/events/${eventId}`);
    // revalidatePath('/profile');
    return result;
  } catch (error) {
    return { success: false, message: 'An error occurred while joining the event.' };
  }
}

