'use client';

import { useFormState, useFormStatus } from 'react-dom';
import Link from 'next/link';
import { login } from '@/lib/actions';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { LogIn, AlertCircle } from 'lucide-react';

function SubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" className="w-full" disabled={pending}>
      {pending ? (
        <>
          <LogIn className="mr-2 h-4 w-4 animate-spin" /> Signing In...
        </>
      ) : (
        <>
          <LogIn className="mr-2 h-4 w-4" /> Sign In
        </>
      )}
    </Button>
  );
}

export default function LoginPage() {
  const initialState = { message: null, errors: {} };
  const [state, dispatch] = useFormState(login, initialState);

  return (
    <div className="flex items-center justify-center min-h-[calc(100vh-15rem)] py-12">
      <Card className="w-full max-w-md shadow-xl">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold">Login to MineVent</CardTitle>
          <CardDescription>
            Access your events and profile. <br /> (Hint: Try user 'Steve' or 'Alex', password 'password')
          </CardDescription>
        </CardHeader>
        <form action={dispatch}>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="email">Username (or Email)</Label>
              <Input
                id="email"
                name="email"
                type="text" // Blessing Skin Server often uses username
                placeholder="e.g., Steve"
                required
                autoComplete="username"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input
                id="password"
                name="password"
                type="password"
                placeholder="••••••••"
                required
                autoComplete="current-password"
              />
            </div>
            {state?.message && (
              <div className="flex items-center space-x-2 text-sm text-destructive bg-destructive/10 p-3 rounded-md">
                <AlertCircle className="h-4 w-4" />
                <p>{state.message}</p>
              </div>
            )}
          </CardContent>
          <CardFooter className="flex flex-col gap-4">
            <SubmitButton />
            <p className="text-sm text-muted-foreground">
              Don&apos;t have an account?{' '}
              <Link href="#" className="font-medium text-primary hover:underline">
                Sign up via Blessing Skin Server (mock)
              </Link>
            </p>
          </CardFooter>
        </form>
      </Card>
    </div>
  );
}
